<template>
  <div>
    <van-nav-bar
      title="国泰君安"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />

    <div class="menu_list" v-for="(item,index) in list">
      <div class="menu_item" @click="toNextPage(index)">
        <div class="menu_name">{{item}}</div>
        <div class="meun_arrow"></div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "detail",
    data(){
      return {
        list: ['利率互换', '信用违约互换', '结构金融日报', '业务人员联系方式', '免责声明', '运营管理'],
        nextPagePath: ['', '', '', '', '', '']
      }
    },
    methods:{
      onClickLeft(){
        this.$router.back();
      },
      toNextPage(index){
        switch (index) {
          case 0:
            break;
          case 1:
            break;
          case 2:
            break;
          case 3:
            break;
          case 4:
            break;
          case 5:
            break;
        }
      }
    },
    created() {

    }
  }
</script>

<style scoped>
  .menu_list {
    padding-left: 15px;
  }
  .menu_item {
    height: 46.5px;
    border-bottom: 0.5px solid rgba(238,238,238,1);
    position: relative;
  }
  .menu_name {
    position: absolute;
    top: 11.5px;
    font-size: 16px;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: rgba(32, 32, 32, 1);
    line-height: 22.5px;
  }

  .meun_arrow {
    position: absolute;
    top: 17px;
    right: 15px;
    width: 6.5px;
    height: 11.5px;
    background: url("../../assets/images/arrow.png");
    background-size: cover;
  }
</style>



